const geoUrl =
  "https://raw.githubusercontent.com/subyfly/topojson/master/world-countries.json";


const RESOURCES = {
  ENERGY: 'energy',
  WATER: 'water',
  FOOD: 'food',
  AIR_QUALITY: 'airQuality',
};

const ClimaDrive = () => {
  const [user, setUser] = useState({displayName:'',email:''});
  const [gameMode, setGameMode] = useState('');
  const [level, setLevel] = useState(1);
  const [experience, setExperience] = useState(0);
  const [openEventDialog, setOpenEventDialog] = useState(false);
  const [currentEvent, setCurrentEvent] = useState({ name:'', effect:{ sustainability: 0, happiness: 0, pollution: 0 , population : 0, water : 0,   food : 0, energy : 0, budget: 0  } });
  const [selectedMode, setSelectedMode ] = useState({ name: '', budget: 0, goal: '' }); 
  const [selectedCity, setSelectedCity] = useState(null); 
  const [stage, setStage] = useState("login");

  const [timer, setTimer] = useState(600); // 타이머: 10분 (600초)
  //const [backgroundImage, setBackgroundImage] = useState(backgroundImages.default);

interface cityStyleType {
   backgroundImage: string;
   backgroundSize: string;
   backgroundPosition: string;
   height: string;
   width: string;
   alignItems: string;
}
const cityStyle : cityStyleType = {
  backgroundImage: `url(images/${selectedCity}.jpg)`,
  backgroundSize: "cover",
  backgroundPosition: "center",
  height: "100vh",
  width: "100vw",
  alignItems: "center"
};

  let cities = [
    { name: "newyork", x: -74.006, y :40.7128 },
    { name: "seoul", x: 126.978, y :37.5665 },
    { name: "london", x: -0.1276, y : 51.5074 }
    // 추가 도시들...
  ];
 
const [gameState, setGameState] = useState({
  year: 2050,
  sustainability: 50,
  happiness: 70,
  pollution: 30,
  population: 1000000,
  water :0, 
  food : 0,
  energy : 0,
  budget: 1000000,
});



  const [projects, setProjects] = useState([
    { name: '태양광 발전소', cost: 500000, sustainability: 10, resource: 'energy', pollution: 0, minLevel: 1 },
    { name: '수처리 시설', cost: 300000, sustainability: 5, resource: 'water', pollution: 0, minLevel: 1 },
    { name: '수직 농장', cost: 400000, sustainability: 7, resource: 'food', pollution: 0, minLevel: 2 },
    { name: '전기차 인프라', cost: 600000, sustainability: 12, resource: 'energy', pollution: 0, minLevel: 3 },
    { name: '스마트 그리드', cost: 800000, sustainability: 15, resource: 'energy', pollution: 0, minLevel: 4 },
    { name: '탄소 포집 시설', cost: 1000000, sustainability: 20, resource: 'energy', pollution: -10, minLevel: 5 },
  ]);

  const gameModes = [
    { name: '청소년', budget: 100000, goal: '학교 주변 쓰레기 줍기' },
    { name: '시민', budget: 200000, goal: '동네 재활용 프로그램 운영' },
    { name: '활동가', budget: 300000, goal: '도시 전체 자전거 도로 확충' },
    { name: '기업가', budget: 1000000, goal: '친환경 기업 10개 육성' },
    { name: '단체장', budget: 2000000, goal: '도시 탄소 배출량 30% 감축' },
    { name: '시장', budget: 5000000, goal: '도시 전체 재생에너지 50% 달성' },
    { name: '대통령', budget: 100000000, goal: '국가 탄소 중립 달성' },
  ];

  /* 기후위기 */
  const CLIMAS = [
    { name: '폭염', effect: { sustainability: -5,  happiness: -10, pollution: 5 , population : 0, water : 0,   food : 0, energy : 0,  budget : 0 } },
    { name: '홍수', effect: { sustainability: -10, happiness: -15, pollution: 0 , population : 0, water : 0,   food : 0, energy : 0,   budget: -500000 } },
    { name: '가뭄', effect: { sustainability: 0,   happiness: -10, pollution: 0 , population : 0, water : -20, food : -15, energy : 0, budget : 0 } },
    { name: '대기오염', effect: { sustainability: -8, happiness: -12, pollution: 10, population : 0, water : 0, food : 0, energy : 0, budget : 0 } },
    { name: '해수면 상승', effect: { sustainability: -12, happiness: 0, pollution: 0 ,population: -50000, water : 0, food : 0, energy : 0, budget : -800000 } }
  ]; 


  const [resources, setResources] = useState({
    [RESOURCES.ENERGY]: 100,
    [RESOURCES.WATER]: 100,
    [RESOURCES.FOOD]: 100,
    [RESOURCES.AIR_QUALITY]: 100,
  });
  const renderResourceIcon = (resource : any) => {
    switch(resource) {
      case RESOURCES.ENERGY: return <Zap className="mr-2" />;
      case RESOURCES.WATER: return <Droplet className="mr-2" />;
      case RESOURCES.FOOD: return <Trees className="mr-2" />;
      case RESOURCES.AIR_QUALITY: return <Wind className="mr-2" />;
      default: return null;
    }
  };

  const handleGoogleLogin = () => {
    // 실제 구글 로그인 로직 구현 필요
    setUser({ displayName: '사용자', email: 'test@example.com' });
    setStage("selectCity");
  };

  const handleLogout = () => {
    setUser({displayName:'',email:''});
    setGameMode('');
    setLevel(1);
    setExperience(0);
    setSelectedCity(null);
    setResources({
      [RESOURCES.ENERGY]: 100,
      [RESOURCES.WATER]: 100,
      [RESOURCES.FOOD]: 100,
      [RESOURCES.AIR_QUALITY]: 100,
    });
    setGameState({ year: 2050, sustainability: 50,  happiness: 70,  pollution: 30,  population: 1000000,  water :0,   food : 0,  energy : 0,  budget: 1000000});

  };

   const handleModeLogout = () => {  
    setSelectedCity(null);
  };

  const handleCitySelect = (city : any) => {
    console.log('------------------------citySelect --------' + city);
    setSelectedCity(city.name);
  };

  const handleModeSelect = (mode : string ) => {

    console.log('-----------mode------------');
    console.log(mode);
    console.log('-----------selectedMode------------');
    console.log(gameMode);
    setGameMode(mode); 
    /*
    type selectedModeType = { 
      [index: string]: strging | number
      name: string
      budget: number 
      goal: string 
    }
    */    
    const selectedMode = gameModes.find(m => m.name === mode);  

    var K = gameModes.find(m => m.name === mode)?.budget as number;

    setGameState(prev => ({ ...prev, budget: K }));  

    console.log('-----------gameMode------------');
    console.log(gameModes.find(m => m.name === mode));

 
  };


// 랜덤 이벤트 발생 함수
 const triggerRandomEvent = () => {
  const randomEvent = Math.random();
  /*
  if (randomEvent < 0.3) {
    setEventMessage("허리케인이 발생했습니다. 복구 비용이 듭니다.");
    setBudget((prevBudget) => prevBudget - 50000);
  } else if (randomEvent < 0.6) {
    setEventMessage("가뭄으로 식량 생산량이 감소했습니다.");
    setFood((prevFood) => (prevFood > 10 ? prevFood - 10 : 0));
  } else {
    setEventMessage("모든 것이 평온합니다.");
  }
  */

};


  useEffect(() => {
    const timer = setInterval(() => {
       setTimer((prevTimer) => (prevTimer > 0 ? prevTimer - 1 : 0));
      setGameState(prevState => ({
          ...prevState,
          year: prevState.year + 1,
          sustainability: Math.max(0, prevState.sustainability - 1),
          happiness: Math.max(0, Math.min(100, prevState.happiness + (Math.random() * 10 - 5))),
          pollution: Math.max(0, Math.min(100, prevState.pollution + (Math.random() * 5 - 2))),
      }));
      setExperience(prev => {
        const newExp = prev + 10;
        if (newExp >= 100 * level) {
          setLevel(prevLevel => prevLevel + 1);
          return 0;
        }
        return newExp;
      });

      // 랜덤 이벤트 발생
      if (Math.random() < 0.1) {  // 10% 확률로 이벤트 발생
        
        const randomClimas = CLIMAS[Math.floor(Math.random() * CLIMAS.length)]; 
        console.log('---------random-----------');
        console.log(randomClimas); 
        setCurrentEvent(randomClimas); 
        setOpenEventDialog(true);

        // 이벤트 효과 적용        
        Object.entries(randomClimas.effect).forEach(([key, value]) => {
          if (key === 'energy' || key === "food" || key === "water") {
          //  newState.sustainability = Math.max(0, Math.min(100, newState.sustainability + value));
            setResources(prev => ({ ...prev, [key]: Math.max(0, Math.min(100, prev[key] + value))}));
          } else {
            setGameState(prevState => ({ ...prevState, 
              budget: prevState.budget + value,
              sustainability: prevState.sustainability + value,
              happiness: prevState.happiness + value,
              pollution: prevState.pollution + value,
              population: prevState.population + value
          }))
            //setResources(prev => ({ ...prev, [key]: Math.max(0, Math.min(100, prev[key] + value))}));
          }
        });
        //return newState;
        
      } 

      /*
      setYear((prevYear) => prevYear + 1);
      setSustainability((prevSustainability) =>
        prevSustainability > 0 ? prevSustainability - 1 : 0
      ); 
      */
      triggerRandomEvent(); // 랜덤 이벤트 발생

    }, 5000); // 10초마다 1년 경과  10000 10초

    return () => clearInterval(timer);
  }, [level, CLIMAS]);

  const startProject = (project : any ) => {
    
    if (gameState.budget >= project.cost && level >= project.minLevel) {
      setGameState(prevState => ({
        ...prevState,
        budget: prevState.budget - project.cost,
        /* sustainability: prevState.sustainability + project.sustainability, */
        sustainability: Math.min(100, prevState.sustainability + project.sustainability),
        pollution: Math.max(0, prevState.pollution + (project.pollution || 0)),
      }));
      
      console.log('---------project----------');
      console.log(project.resource);
      //console.log(Math.min(100, preResources['water'] + 10 )); 
      /*
      setResources(preResources =>({
        ...preResources, [project.resource] : Math.min(100, preResources['water'] + 10 )
      }))
      */
      
      setResources(prevResources => ({
        ...prevResources,
        [project.resource]: Math.min(100, prevResources[project.resource] + 10),
      })); 
      setExperience(prev => prev + 20);
    }
  };

  const getLevelTitle = () => {
    if (level <= 5) return '초급';
    if (level <= 10) return '중급';
    if (level <= 15) return '고급';
    return '최고급';
  };

  const handleEventDialogClose = () => {
    setOpenEventDialog(false);

    if(currentEvent){
      console.log('-------------currentEvent------------'); 
      console.log(currentEvent.effect);
    }else{
    
      console.log('-----------NO--currentEvent------------'); 
    }
    //console.log(Object.entries( currentEvent.effect ));
    //setCurrentEvent({name:'', effect:{sustainability: 0, happiness: 0, pollution: 0, population : 0, water : 0,   food : 0,   budget:0 }});

    /*
    if (currentEvent) {
      setGameState(prevState => ({
        ...prevState,
        ...Object.fromEntries(
          Object.entries( currentEvent.effect).map(([key, value]) => [key, prevState[key] + value ])
        )
      }));
      //setCurrentEvent({name:'', effect:{ sustainability: 0, happiness: 0, pollution: 0, population : 0, water : 0,   food : 0, energy : 0,  budget:0 }});
    }
    */
    
    
  };

  if (user.displayName == '') {
    return ( 

      <Container style={imgStyle}>
      <ThemeProvider theme={theme} >
        <Container maxWidth="sm"  style={{ paddingTop :"50%" }}>
          {/*          
          <GoogleLogin onSuccess={credentialResponse => {
                console.log(credentialResponse);
            }}
            onError={() => {
                console.log('Login Failed');
            }}
          />; 
          */}
          <Button variant="contained" color="secondary" fullWidth onClick={handleGoogleLogin}>
            Google 로그인
          </Button>
        </Container>
      </ThemeProvider> 
      </Container>

    );
  }


  if (!selectedCity) {
    return (       
      <ThemeProvider theme={theme}> 

      <AppBar position="static">
        <Toolbar>
          <Button color="inherit" onClick={handleLogout}>HOME</Button>
          <Typography variant="subtitle1" style={{ marginRight: '20px' }}>도시를 선택하세요</Typography>
        </Toolbar>
      </AppBar>
 

      <ComposableMap>
        <Geographies geography={geoUrl}>
          {({ geographies }) =>
            geographies.map((geo) => (
              <Geography key={geo.rsmKey} geography={geo} fill="#EAEAEC" stroke="#D6D6DA" />
            ))
          }
        </Geographies>
        {cities.map((city) => (
          <Marker key={city.name} coordinates={[ city.x, city.y] }  onClick={() => handleCitySelect(city)} >
            <circle r={5} fill="#F53" stroke="#fff" strokeWidth={2} />
            <text
              textAnchor="middle"
              y={-10}
              style={{ fontFamily: "system-ui", fill: "#5D5A6D" }}
              onClick={() => handleCitySelect(city)}
            >
              {city.name}
            </text>
          </Marker>
        ))}
      </ComposableMap>  
      </ThemeProvider> 
    );
  }



/****************IMAGE BOX***************** */
const images = [
  {
    url: 'images/youth.png',
    title: '청소년',
    width: '16%',
  },
  {
    url: 'images/citizen.png',
    title: '시민',
    width: '16%',
  },
  {
    url: 'images/activity.png',
    title: '활동가',
    width: '16%',
  },
  {
    url: 'images/company.png',
    title: '기업가',
    width: '16%',
  },
  {
    url: 'images/president.png',
    title: '시장',
    width: '18%',
  },
  {
    url: 'images/president.png',
    title: '대통령',
    width: '18%',
  },
];

const ImageButton = styled(ButtonBase)(({ theme }) => ({
  position: 'relative',
  height: 200,
  [theme.breakpoints.down('sm')]: {
    width: '100% !important', // Overrides inline-style
    height: 100,
  },
  '&:hover, &.Mui-focusVisible': {
    zIndex: 1,
    '& .MuiImageBackdrop-root': {
      opacity: 0.15,
    },
    '& .MuiImageMarked-root': {
      opacity: 0,
    },
    '& .MuiTypography-root': {
      border: '4px solid currentColor',
    },
  },
}));

const ImageSrc = styled('span')({
  position: 'absolute',
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  backgroundSize: 'cover',
  backgroundPosition: 'center 40%',
});

const Image = styled('span')(({ theme }) => ({
  position: 'absolute',
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
  color: theme.palette.common.white,
}));

const ImageBackdrop = styled('span')(({ theme }) => ({
  position: 'absolute',
  left: 0,
  right: 0,
  top: 0,
  bottom: 0,
  backgroundColor: theme.palette.common.black,
  opacity: 0.4,
  transition: theme.transitions.create('opacity'),
}));

const ImageMarked = styled('span')(({ theme }) => ({
  height: 3,
  width: 18,
  backgroundColor: theme.palette.common.white,
  position: 'absolute',
  bottom: -2,
  left: 'calc(50% - 9px)',
  transition: theme.transitions.create('opacity'),
}));
/********************************* */


  if ( selectedCity && !gameMode ) {
    return (
      <Container style={cityStyle}> 
      <ThemeProvider theme={theme} > 
      <AppBar position="static">
        <Toolbar>
          <Button color="secondary" onClick={handleLogout}>HOME</Button>  
          <Button color="secondary" onClick={handleModeLogout}>도시선택</Button> 
          <Typography variant="subtitle1" style={{ marginRight: '20px' }}>직업 모드를 선택하세요</Typography>
        </Toolbar>
      </AppBar>

      <Container style={{ marginTop: 50 }}></Container>
      <Box sx={{ display: 'flex', flexWrap: 'wrap', minWidth: 500, width: '100%' }}>
      {images.map((image) => (
        <ImageButton
          focusRipple
          key={image.title}
          style={{
            width: image.width,
          }}

          onClick={() => handleModeSelect(image.title) }
        >
          <ImageSrc style={{ backgroundImage: `url(${image.url})` }} />
          <ImageBackdrop className="MuiImageBackdrop-root" />
          <Image>
            <Typography
              component="span"
              variant="subtitle1"
              color="inherit"
              sx={(theme) => ({
                position: 'relative',
                p: 4,
                pt: 2,
                pb: `calc(${theme.spacing(1)} + 6px)`,
              })}
            >
              {image.title}
              <ImageMarked className="MuiImageMarked-root" />
            </Typography>
          </Image>
        </ImageButton>
      ))}
    </Box> 
          <Select
            fullWidth
            value={gameMode}
            onChange={(e) => handleModeSelect(e.target.value)}
          >
            {gameModes.map(mode => (
              <MenuItem key={mode.name} value={mode.name}>
                {mode.name} - 목표: {mode.goal}
              </MenuItem>
            ))}
          </Select>  
      </ThemeProvider>
      </Container> 
    );
  }

  return (

    <Container style={cityStyle} >
    <ThemeProvider theme={theme} >
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" style={{ flexGrow: 1 }}>ClimaDrive  </Typography>
          <Typography variant="subtitle1" style={{ marginRight: '20px' }}>
            {selectedCity} {user.displayName} ({gameMode})
          </Typography>
          <Button color="inherit" onClick={handleLogout}>로그아웃</Button>
        </Toolbar>
      </AppBar>  
          
      <Container maxWidth="lg" style={{ marginTop: '20px'}} >
      <Card className="mb-4">
        <CardHeader title={ gameModes.find(m => m.name === gameMode)?.goal } >  </CardHeader> 
        <Typography variant="h6">
        기후 위기 타이머: {Math.floor(timer / 60)}분 {timer % 60}초
        </Typography>
      </Card>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6}>
            <Card>
              <CardHeader title="도시 현황" />
              <CardContent>
                <Typography>년도: {gameState.year}</Typography>
                <Typography>인구: {gameState.population.toLocaleString()}</Typography>
                <Typography>예산: ${gameState.budget.toLocaleString()}</Typography>
                <Typography>지속 가능성: {gameState.sustainability}%</Typography>
                <LinearProgress variant="determinate" value={gameState.sustainability} />
                <Typography>행복도: {gameState.happiness}%</Typography>
                <LinearProgress variant="determinate" value={gameState.happiness} color="secondary" />
                <Typography>오염도: {gameState.pollution}%</Typography>
                <LinearProgress variant="determinate" value={gameState.pollution} color="error" />
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12} md={6}>
            <Card>
              <CardHeader title="자원" />
              <CardContent>
                {Object.entries(resources).map(([key, value]) => (
                  <div key={key} className="flex items-center mb-2">
                    {renderResourceIcon(key)} {key}: {value}%
                    <LinearProgress value={value}  variant="determinate" className="ml-2 flex-grow" />
                  </div>
                ))}
                {/*
                <Typography>에너지: {resources.energy}%</Typography>
                <LinearProgress variant="determinate" value={resources.energy} />
                <Typography>물: {resources.water}%</Typography>
                <LinearProgress variant="determinate" value={resources.water} />
                <Typography>식량: {resources.food}%</Typography>
                <LinearProgress variant="determinate" value={resources.food} />
              */}
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12}>
            <Card>
              <CardHeader title="레벨 정보" />
              <CardContent>
                <Typography>레벨: {level} ({getLevelTitle()})</Typography>
                <LinearProgress variant="determinate" value={(experience / (100 * level)) * 100} />
              </CardContent>
            </Card>
          </Grid>
          <Grid item xs={12}>
            <Card>
              <CardHeader title="프로젝트" />
              <CardContent>
                <Grid container spacing={2}>
                  {projects.map((project, index) => (
                    <Grid item xs={12} sm={6} md={4} key={index}>
                      <Button
                        variant="contained"
                        color="primary"
                        fullWidth
                        onClick={() => startProject(project)}
                        disabled={gameState.budget < project.cost || level < project.minLevel}
                      >
                        {project.name} (${project.cost.toLocaleString()})
                        {level < project.minLevel && ` (레벨 ${project.minLevel} 필요)`}
                      </Button>
                    </Grid>
                  ))}
                </Grid>
              </CardContent>
            </Card>
          </Grid>
        </Grid>
      </Container>
      <Dialog open={openEventDialog} onClose={handleEventDialogClose}>
        <DialogTitle>긴급 상황 발생!</DialogTitle>
        <DialogContent>
          {currentEvent && (
            <Typography>
              {currentEvent.name} 발생! 
              {Object.entries(currentEvent.effect).map(([key, value]) => (
                <Box key={key}>{key}: {value > 0 ? '+' : ''}{value}</Box>
              ))}
            </Typography>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleEventDialogClose} color="primary">
            확인
          </Button>
        </DialogActions>
      </Dialog>
    </ThemeProvider>
    </Container>
  );
};

export default ClimaDrive;